___CSS_LOADER_EXPORT___.push([module.id, `/* ===== SEARCH BOX ===== */
.search {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 18;
    min-width: 200px;
}

.search_row {
    list-style-type: none;
    font-family: monospace;
    cursor: pointer;
    width: 200px;
}

.search_row.active {
    background-color: lightblue;
}

.search_result_list {
    position: static;
    list-style: none;
    margin-left: 0;
    padding-left: 1.2em;
    margin-bottom: 1px;
    border-radius: 0 5px 0 5px;
    filter: drop-shadow(0 0 1px #333);
    max-width: 500px;
    overflow: hidden;
    min-width: 200px;
    width: fit-content;
    width: -moz-fit-content;

}

.filter {
    cursor: pointer;
    font-family: monospace;
    border-radius: 500px;
    left: 0;
    padding-left: 20px;
    opacity: 0.8;
    width: 200px;
}

.active_filters_list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.close {
    color: black;
    float: right;
    padding-right: 5px;
}

.close:hover {
    opacity: 1;
}

form {
    padding-top: 0;
}
