    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    line-height: 1.5em;
    min-height: 1.5em;
}

.box_border {
    border-radius: 1px 1px 1px 1px;
    border: 2px solid;
    margin: 25px 5px 15px 5px;
    position: relative;
}

.mouse_over_shadow:hover {
    transition: drop-shadow .1s;
    filter: drop-shadow(0 0 2px #333);
}

.highlighted_box {
    transition: drop-shadow .1s;
    filter: drop-shadow(0 0 2px #333);
}

