___CSS_LOADER_EXPORT___.push([module.id, `.set_container {
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    line-height: 1.5em;
    min-height: 1.5em;
}

.node_border {
    border-radius: 10px 10px 10px 10px;
    border: 2px solid;
    margin: 15px 5px 15px 5px;
    position: relative;
    height: max-content;
}

.mouse_over_shadow:hover {
    transition: drop-shadow .1s;
    filter: drop-shadow(0 0 2px #333);
}

.highlighted_node {
    transition: drop-shadow .1s;
    filter: drop-shadow(0 0 2px #333);
}

.bauchbinde {
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius: 4px 0px 8px 0px;
}

.bauchbinde:hover {
    transition: box-shadow .2s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.2);
    transition: .2s ease-in-out;
    -moz-transition: .2s ease-in-out;
    -webkit-transition: .2s ease-in-out;
}

.bauchbinde_text {
    cursor: pointer;
    font-size: 1pt;
    vertical-align: middle;
    text-align: center;
}

.set_too_high {
    overflow-y: hidden;
}

.set_value {
    font-family: monospace;
    font-size: 14pt;
    width: -moz-fit-content;
    width: fit-content;
    margin: 5px;
    min-width: 15px;

    align-items: flex-start;
    position: relative;
    flex: 0 0 auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
}

.set_container {
    cursor: pointer;
}

.mini {
    text-align: center;
    vertical-align: middle;
    width: fit-content;
    height: fit-content;
    font-size: 12pt;
    min-width: 8px;
    cursor: pointer;
    min-height: 8px;
}

.recursion_button{
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 0px 8px 0px 4px;
}

.recursion_button:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.2);
    transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -webkit-transition: .3s ease-in-out;
}

.recursion_button_text{
    cursor: pointer;
    font-size: 1pt;
    font-weight: bold;
    border-radius: 0px 8px 0px 4px;
    padding: 1px;
    vertical-align: middle;
    text-align: center;
    width: auto;
}`, "",{"version":3,"sources":["webpack://./src/lib/components/node.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,kBAAkB;IAClB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,kCAAkC;IAClC,iBAAiB;IACjB,yBAAyB;IACzB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,2BAA2B;IAC3B,iCAAiC;AACrC;;AAEA;IACI,2BAA2B;IAC3B,iCAAiC;AACrC;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,8BAA8B;AAClC;;AAEA;IACI,0BAA0B;IAC1B,2EAA2E;IAC3E,2BAA2B;IAC3B,gCAAgC;IAChC,mCAAmC;AACvC;;AAEA;IACI,eAAe;IACf,cAAc;IACd,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,eAAe;IACf,uBAAuB;IACvB,kBAAkB;IAClB,WAAW;IACX,eAAe;;IAEf,uBAAuB;IACvB,kBAAkB;IAClB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,2BAA2B;AAC/B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;IACf,cAAc;IACd,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,MAAM;IACN,8BAA8B;AAClC;;AAEA;IACI,2EAA2E;IAC3E,2BAA2B;IAC3B,gCAAgC;IAChC,mCAAmC;AACvC;;AAEA;IACI,eAAe;IACf,cAAc;IACd,iBAAiB;IACjB,8BAA8B;IAC9B,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;IAClB,WAAW;AACf","sourcesContent":[".set_container {\n    width: -moz-fit-content;\n    width: fit-content;\n    position: relative;\n    line-height: 1.5em;\n    min-height: 1.5em;\n}\n\n.node_border {\n    border-radius: 10px 10px 10px 10px;\n    border: 2px solid;\n    margin: 15px 5px 15px 5px;\n    position: relative;\n    height: max-content;\n}\n\n.mouse_over_shadow:hover {\n    transition: drop-shadow .1s;\n    filter: drop-shadow(0 0 2px #333);\n}\n\n.highlighted_node {\n    transition: drop-shadow .1s;\n    filter: drop-shadow(0 0 2px #333);\n}\n\n.bauchbinde {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    border-radius: 4px 0px 8px 0px;\n}\n\n.bauchbinde:hover {\n    transition: box-shadow .2s;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.2);\n    transition: .2s ease-in-out;\n    -moz-transition: .2s ease-in-out;\n    -webkit-transition: .2s ease-in-out;\n}\n\n.bauchbinde_text {\n    cursor: pointer;\n   